<template>
  <TerminalBlock style="width: 220px">
    <div class="prescription">
      <slot />
    </div>
    <div id="skin-viewer" class="mc-skin-viewer-9x spin">
      <div class="player">
        <!-- Head -->
        <div class="head">
          <div class="top"></div>
          <div class="left"></div>
          <div class="front"></div>
          <div class="right"></div>
          <div class="back"></div>
          <div class="bottom"></div>
          <div class="accessory">
            <div class="top"></div>
            <div class="left"></div>
            <div class="front"></div>
            <div class="right"></div>
            <div class="back"></div>
            <div class="bottom"></div>
          </div>
        </div>
        <!-- Body -->
        <div class="body">
          <div class="top"></div>
          <div class="left"></div>
          <div class="front"></div>
          <div class="right"></div>
          <div class="back"></div>
          <div class="bottom"></div>
          <div class="accessory">
            <div class="top"></div>
            <div class="left"></div>
            <div class="front"></div>
            <div class="right"></div>
            <div class="back"></div>
            <div class="bottom"></div>
          </div>
        </div>
        <!-- Left Arm -->
        <div class="left-arm">
          <div class="top"></div>
          <div class="left"></div>
          <div class="front"></div>
          <div class="right"></div>
          <div class="back"></div>
          <div class="bottom"></div>
          <div class="accessory">
            <div class="top"></div>
            <div class="left"></div>
            <div class="front"></div>
            <div class="right"></div>
            <div class="back"></div>
            <div class="bottom"></div>
          </div>
        </div>
        <!-- Right Arm -->
        <div class="right-arm">
          <div class="top"></div>
          <div class="left"></div>
          <div class="front"></div>
          <div class="right"></div>
          <div class="back"></div>
          <div class="bottom"></div>
          <div class="accessory">
            <div class="top"></div>
            <div class="left"></div>
            <div class="front"></div>
            <div class="right"></div>
            <div class="back"></div>
            <div class="bottom"></div>
          </div>
        </div>
        <!-- Left Leg -->
        <div class="left-leg">
          <div class="top"></div>
          <div class="left"></div>
          <div class="front"></div>
          <div class="right"></div>
          <div class="back"></div>
          <div class="bottom"></div>
          <div class="accessory">
            <div class="top"></div>
            <div class="left"></div>
            <div class="front"></div>
            <div class="right"></div>
            <div class="back"></div>
            <div class="bottom"></div>
          </div>
        </div>
        <!-- Right Leg -->
        <div class="right-leg">
          <div class="top"></div>
          <div class="left"></div>
          <div class="front"></div>
          <div class="right"></div>
          <div class="back"></div>
          <div class="bottom"></div>
          <div class="accessory">
            <div class="top"></div>
            <div class="left"></div>
            <div class="front"></div>
            <div class="right"></div>
            <div class="back"></div>
            <div class="bottom"></div>
          </div>
        </div>
      </div>
    </div>
  </TerminalBlock>
</template>

<script>
import TerminalBlock from "@/components/TerminalBlock";
export default {
  name: "SkinViewer",
  components: { TerminalBlock },
  props: {
    skinLink: {
      type: String,
      default: "",
    },
  },
  computed: {
    skinCSSProp() {
      return `url("${this.skinLink}")`;
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/scss/skinViewer/mc-skin-viewer.scss";

#skin-viewer * {
  background-image: v-bind(skinCSSProp);
}
.prescription {
  color: white;
  line-height: 1.1;
  font-weight: bold;
  font-style: italic;
  margin-bottom: 25px;
  font-size: 12px;
  font-family: "Press Start 2P", cursive;
}
</style>
