<template>
  <div class="window">
    <div class="title-bar">
      <div class="title-bar-text">
        <Icon icon="noto-v1:pushpin" v-if="pageMode" /> {{ title }}
      </div>
      <div class="title-bar-controls">
        <button
          aria-label="Minimize"
          v-if="showButtonMinimize"
          @click="$emit('minimize-click')"
        />
        <button
          aria-label="Maximize"
          v-if="showButtonMaximize"
          @click="$emit('maximize-click')"
        />
        <button
          aria-label="Close"
          v-if="showButtonClose"
          @click="$emit('close-click')"
        />
      </div>
    </div>
    <div class="window-body">
      <slot />
    </div>
  </div>
</template>

<script>
import { Icon } from "@iconify/vue";

export default {
  name: "Window",
  components: { Icon },
  props: {
    title: {
      type: String,
      default: "",
    },
    showButtonMinimize: {
      type: Boolean,
      default: false,
    },
    showButtonMaximize: {
      type: Boolean,
      default: false,
    },
    showButtonClose: {
      type: Boolean,
      default: false,
    },
    pageMode: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

<style scoped lang="scss">
.window {
  overflow: hidden;
}
.window-body {
  height: calc(100% - 36px);
  overflow-y: scroll;
  overflow-x: hidden;
}
</style>
