<template>
  <h1 class="identity">{{ displayIdentity }}</h1>
  <div class="appearence">
    {{
      $store.state.character.current_appearance ||
      $store.state.character.appearance
    }}
  </div>
</template>

<script>
export default {
  name: "Identity",
  computed: {
    displayIdentity() {
      let identity = this.$store.state.character.display_name;
      let splittedIdentity = identity.split(" ");
      if (splittedIdentity.length === 1) return identity;
      return splittedIdentity
        .map((item, index) => {
          if (index < splittedIdentity.length - 1) return item[0] + ".";
          else return item;
        })
        .join("");
    },
  },
};
</script>

<style scoped lang="scss">
.appearence {
  text-align: left;
}
.identity {
  font-size: 18px;
}
</style>
