<template>
  <div class="load-bar clear-bg">
    <div class="load-bar__bar" />
  </div>
</template>

<script>
export default {
  name: "LoadBar",
  props: {
    colors: {
      type: Array,
      default: () => ["#426b77", "#6099b9"],
    },
    maximum: {
      type: Number,
      default: 100,
    },
    current: {
      type: Number,
      default: 50,
    },
  },
  computed: {
    loadPercentStyle() {
      return 100 / (this.maximum / this.current) + "%";
    },
    loadBarStyle() {
      return `repeating-linear-gradient(
      90deg,
      ${this.colors[0]},
      ${this.colors[1]} 20px,
      white 20px,
      white 21px
    )`;
    },
  },
};
</script>

<style lang="scss">
.load-bar {
  height: 18px;
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  display: flex;
  align-items: center;
  border: 3px ridge;

  &__bar {
    width: v-bind(loadPercentStyle);
    height: 100%;
    background: v-bind(loadBarStyle);
  }
}
</style>
